<template>
  <div class="page-login">
    <div class="loading-wrap">
      <van-loading size="24px">加载中...</van-loading>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { mapState, mapActions } from 'vuex'
import qs from 'querystring'
import * as dd from 'dingtalk-jsapi'
import { Button, Toast, Dialog, Loading } from 'vant'
import request from '@/utils/request'
import { setToken } from '@/utils/token'

Vue.use(Toast)
  .use(Dialog)
  .use(Loading)

export default {
  name: 'login',
  components: {},
  data() {
    return {
      code: ''
    }
  },
  computed: {},
  created() {
    const vm = this
    dd.ready(function() {
      // dd.ready参数为回调函数，在环境准备就绪时触发，jsapi的调用需要保证在该回调函数触发后调用，否则无效。
      dd.runtime.permission.requestAuthCode({
        corpId: 'ding6ab7fb6fbb656216acaaa37764f94726',
        onSuccess: function(info) {
          dd.device.notification.alert({
            message: info.code,
            title: 'code值', // 可传空
            buttonName: '确定',
            onSuccess: function() {},
            onFail: function() {}
          })
          vm.doLogin(info.code)
        },
        onFail: function(error) {
          dd.device.notification.alert({
            message: error,
            title: '提示', // 可传空
            buttonName: '确定',
            onSuccess: function() {},
            onFail: function() {}
          })
        }
      })
    })
  },
  methods: {
    ...mapActions('login', ['getLoginUserInfo']),
    async doLogin(code) {
      const params = {
        code
      }
      const response = await request(`/auth/fujingLoginByPhoneCheckCode?${qs.stringify(params)}`, {
        method: 'POST'
      })
      if (response.resultCode === 200) {
        setToken(response.data.token)
        Toast.success({ message: response.message, duration: 600 })
        this.getLoginUserInfo()
        setTimeout(() => {
          this.$router.push({
            name: 'punish'
          })
        }, 100)
      } else {
        Toast.fail(response.message)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.loading-wrap {
  padding: 100px 0;
  text-align: center;
}
.bottom {
  margin-top: 100px;
  width: 10rem;
  text-align: center;
  .van-button {
    width: 70%;
  }
}
</style>
